@use './materials/mixin' as *;

.story_section
{
	padding-top: 12rem;
	padding-bottom: 10rem;
	min-height: 100vh;
	// background-color: var(--background-secondary);
	background-color: var(--background-primary);

	.container
	{
		max-width: 110rem;
		margin: auto;
		position: relative;
		.box 
		{
			width: 100%;
			margin: auto;
			display: flex;
			justify-content: center;
			position: relative;
			flex-direction: column;
			border: 1px solid lighten(grey, 20%);
			border-radius: 1.5rem;

			.headingBox
			{
				padding: 1.5rem 2rem;
				border-bottom: 1px solid lighten(grey, 20%);

				h2 
				{
					font-size: 2.2rem;
				}
			}

			.detailBox
			{
				padding: 2rem;
				// display: grid;
				// grid-template-columns: 2fr 1fr;
				// column-gap: 2rem;

				.rightBox
				{
					position: relative;

					.coverPhotoBox
					{
						display: block;
						background: lighten(grey, 45%);
						width: 100%;
						height: 25rem;
						border-radius: 1rem;
						display: flex;
						justify-content: center;
						align-items: center;
						flex-direction: column;
						transition: .5s;
						position: relative;

						&:hover
						{
							background: lighten(grey, 20%);
						}

						img
						{
							width: 100%;
							height: 100%;
							object-fit: cover;
							border-radius: 1rem;
						}

						.icon
						{
							font-size: 5rem;
							color: lighten(grey, 30%);
						}

						.text 
						{
							font-size: 1.5rem;
							color: lighten(grey, 30%);
						}
					}
				}
			}

			.buttonBox
			{
				padding: 2rem;

				button 
				{
					width: 20rem;
					padding: 1rem;
					border-radius: 2rem;
					border: none;
					background-color: #000;
					color: #fff;
					font-size: 1.7rem;
					margin: 0 1.5rem;
					transition: .7s;

					&:hover
					{
						background-color: #fff;
						color: lighten(grey, 10%);
						border: 1px solid lighten(grey, 15%);
					}
				}

				.btn2 
				{
					background-color: #fff;
					color: lighten(grey, 10%);
					border: 1px solid lighten(grey, 15%);
					transition: .7s;

					&:hover 
					{
						background-color: #000;
						color: #fff;
					}
				}
			}

			.feild
			{
				display: flex;
				flex-direction: column;
    			gap: 1rem;
    			position: relative;
    			margin: 2rem 0;

    			.addCharacterMainBox > div > button
    			{
    				padding: .8rem 2rem;
    				font-size: 1.8rem;
    				margin-left: auto;
    				margin-top: .8rem;
    				float: right;
    				border-radius: .5rem;
    				border: .1rem solid black;
    			}

    			.addCharacterBox
    			{
    				padding: 1rem;
    				border: .1rem solid lighten(grey, 20%);
    				border-radius: 1rem;
    				padding-bottom: 3rem;
    			}

    			.two_inputs
    			{
    				width: 100%;
    				height: 100%;
    				display: grid;
    				position: relative;
    				grid-template-colums: repeat(2,1fr);
    				input
    				{
    					width: 50%;
    				}
    			}

    			input 
    			{
				    padding: 1.5rem;
				    // width: 70rem;
				    width: 95.3%;
				    // max-width: 70rem;
				    border-radius: .7rem;
				    border: none;
				    background-color: lighten(grey, 45%);
				    outline: none;
				    font-size: 1.5rem;
    			}

    			textarea
					{
						width: 95.3%;
						height: 20rem;
						padding: 1.5rem;
						border-radius: .7rem;
				    	border: none;
				    	background-color: lighten(grey, 45%);
				    	outline: none;
				    	font-size: 1.5rem;
					}

    			.heading
    			{
    				font-size: 2rem;
    				margin-bottom: .8rem;
    			}

    			.fileInput
    			{
    				display: none;
    			}

    			.add 
    			{
    				display: flex;
    				justify-content: center;
					align-items: center;
					gap: 1rem;
					margin-top: 2rem;

					// input 
					// {
					// 	width: 100%;
					// }

					input[type="file"]
					{
						border: 1px solid;
						border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
						padding: .8rem;
					}

					button
					{
						font-size: 2rem;
						width: 2.5rem;
						height: 2.5rem;
						display: grid;
						place-items: center;
						border-radius: 50%;
						// padding: 1rem 2rem;
						border: 1px solid #000;
						background-color: white;
					}
    			}
			}
		}
	}
}

.useVriable
{
	margin-top: 2rem;
	display: flex;
	align-items: center;
	gap: 1rem;
	button
	{
		font-size: 2rem;
		width: 2.5rem;
		height: 2.5rem;
		display: grid;
		place-items: center;
		border-radius: 50%;
		border: 1px solid #000;
		background-color: white;
	}
}