@use './materials/mixin' as *;


.boxdialog
{
	padding-top: 4rem !important;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 3rem;
}

.input-feild
{
	width: 100%;
	border-bottom: .3rem solid black;
	margin-bottom: 4rem;
	padding-bottom: .5rem;
	input
	{
		border: none;
		background: none;
		outline: none;
		font-size: 1.7rem;
	}
}


// custome example 
.main-container
{
	display: grid;
	grid-template-columns: 8fr 4fr;
	overflow-y: auto;

	.done
	{
		position: absolute;
		bottom: 5%;
		display: flex;
		justify-content: center;
		width: 95%;
	}

	.buttons
	{
		display: block;
		text-align: center;
		font-size: 1.8rem;
		width: 20rem;
		padding: 1rem;
		color: #ffffff;
		background: #D68A63;
		border-radius: 1.5rem;
		margin: 2rem auto;
		border: none;
	}

	.right
	{
		padding: 0 2rem;
		background: #fcf4f0;
		position: relative;
		display: flex;
		flex-direction: column;
		.cloths-box
		{
			display: flex;
			align-items: center;
			border-bottom: 1px solid grey;
			img
			{
				width: 15rem;
				height: 15rem;
			}
			h3
			{
				font-size: 2rem;
			}
		}
		button
		{
			display: block;
			text-align: center;
			font-size: 1.8rem;
			width: 20rem;
			padding: 1rem;
			color: #ffffff;
			background: #D68A63;
			border-radius: 1.5rem;
			margin: 2rem auto;
			border: none;
		}
	}

	.left
	{
		.images
		{
			width: 100%;
			height: 50rem;
			// border: 1px solid;
			position: relative;
			img
			{
				position: absolute;
				border-radius: .5rem;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
		.inputs
		{
			overflow-y: auto;
			// padding-bottom: 1rem;
			padding: 0 1rem 1rem 1rem;
			.field-two-inputs
			{
				display: grid;
				margin-top: 2rem;
				grid-template-columns: 1fr 1fr;
				border: .1rem solid #F4F7F7;
				padding: 1rem;
				border-radius: 1rem;
				gap: .8rem;
				input
				{
					padding: 1.5rem;
					border-radius: .7rem;
					border: none;
					background-color: lighten(grey, 45%);
					outline: none;
					font-size: 1.5rem;
					// border: 1px solid #F4F7F7;
					// border: 1px solid grey;
				}
			}
		}
	}
}

@media (max-width: 750px){
	.main-container
	{
		grid-template-columns: 1fr;
	}
}

.box
{
	padding: 2rem;
}

.btn
{
	display: block;
	text-align: center;
	font-size: 1.8rem;
	width: 20rem;
	padding: 1rem;
	color: #db3529;
	background: lighten(grey, 45%);
	border-radius: 1rem;
	border: none;
	cursor: pointer;
	transition: .5s;

	&:hover
	{
		background: #db3529;
		color: lighten(grey, 45%);
	}
}

.btn:first-child
{
	margin-bottom: 2rem;
	color: #7dbfa2;
	&:hover
	{
		background: #7dbfa2;
		color: lighten(grey, 45%);
	}
}

.coverphoto
{
	width: 100%;
	height: 50rem;

	img
	{
		width: 100%;
		height: 100%;
		object-fit: cover;
		border-radius: 1rem;
	}
}

.btnBox
{
	display: flex;
	gap: 3rem;
	margin-top: 1rem;
	justify-content: center;

	.btn
	{
		margin-bottom: 0;
		width: 50%;
	}

	.storyText
	{
		width: 100%;
	}
}

.storyBox
{
	width: 100%;
	max-width: 100%;
}

.character-container
{
	width: 100%;
	margin-bottom: 2rem;
	overflow-y: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	gap: 2rem;

	.character-box
	{
		width: 20rem;
		padding: 2rem;
		// height: 25rem;
		// border: 1px solid;

		.avatar
		{
			width: 100%;
			height: 18rem;
			margin-bottom: 1rem;
			img
			{
				width: 100%;
				height: 100%;
			}
		}

		.btn
		{
			color: #7dbfa2;
			border-radius: 0;
			&:hover
			{
				background: #7dbfa2;
				color: lighten(grey, 45%);
			}
		}
	}
}

.input-container
{

	.field
	{
		margin: 2rem 0;

		input
		{
			padding: 1.5rem;
			width: 100%;
			max-width: 70rem;
			border-radius: .7rem;
			border: none;
			background-color: lighten(grey, 45%);
			outline: none;
			font-size: 1.7rem;
		}
	}
}

.selected
{
	outline: 1px solid #db3529;
}

.character-customize-box
{
	display: grid;
	grid-template-columns: repeat(2,1fr);

	.left
	{
		width: 20rem;
		height: 20rem;
		position: relative;

		img
		{
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
	}

	.right
	{
		display: flex;
		justify-content: center;
		gap: 1rem;
		overflow-y: auto;
		flex-direction: column;

		div
		{
			display: flex;

			p
			{
				font-size: 1.8rem;
				margin-left: .5rem;
			}
		}
	}
}

.box-character
{
	width: 100%;
	height: 18rem;
	position: relative;

	img
	{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
}

.character-show-box
{
	width: 100%;
	height: 20rem;
	position: relative;

	.avatar
	{
		width: 20rem;
		height: 20rem;
		position: relative;
		margin: auto;

		> img
		{
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
}

.selected-cloth
{
	background: lighten(green, 20%) !important;
	border-color: lighten(green, 20%) !important;
}

.unselected-cloth
{
	background: lighten(red, 20%) !important;
	border-color: lighten(red, 20%) !important;
}

.cloths
{
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.characterBox
{
	border: .1rem solid black;
	margin: 1rem;

	h3
	{
		font-size: 1.8rem;
		text-align: center;
		margin: .5rem 0;
	}
}

.avatar
{
	width: 20rem;
	height: 18rem;
	position: relative;
	img
	{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
}

.inputs
{
	width: 100%;
	.field
	{
		display: flex;
		align-items: center;
		gap: 1rem;
		margin: 2rem;

		> p
		{
			font-size: 1.8rem;
		}

		> input 
    	{
		    padding: 1.5rem;
			// width: 70rem;
			width: 70%;
			// max-width: 70rem;
			border-radius: .7rem;
			border: none;
			background-color: lighten(grey, 45%);
			outline: none;
			font-size: 1.5rem;
    	}
	}
}

.btn-box
{
	border-top: .1rem solid black;
	position: relative;
	display: flex;
	flex-direction: row;
	gap: 0;

	button
		{
			width: 100%;
			text-align: center;
			padding: 1rem 0;
			background: #fff;
			border: 1px solid black;
		}
}

@media (max-width: 500px){
	.character-customize-box
	{
		grid-template-columns: 1fr;
		.left
		{
			margin: auto;
			margin-bottom: 2rem;
		}

		.right
		{
			padding-left: 3rem;
		}
	}
}