@use './materials/mixin' as *;

.card
{
	width: 43.1rem;
	min-height: 40rem;
	box-shadow: 1px 1px 10px 1px lighten(grey,35%);
	border-radius: 1rem;
	background-color: #fff;
	position: relative;
	transition: .5s;
	cursor: pointer;
	position: relative;

	&:hover 
	{
		transform: translateY(-1rem);
	}

	// &:hover .button a
	// {
	// 	color: #fff;
	// 	background-color: #db3529;
	// }

	.image
	{
		display: flex;
		justify-content: center;
		// transform: translateY(-5rem);
		width: 100%;
		position: relative;
		height: 25rem;

		> img
		{
			width: 100%;
			height: 100%; 
			border-radius: 1rem 1rem 0 0;
		}

		.avatar
		{
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			margin: auto;

			> img
			{
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				border-radius: 1rem 1rem 0 0;
			}
		}


	}

	.details
	{
		// transform: translateY(-2rem);
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		padding: 0 1rem;
		gap: 1rem;

		h2
		{
			font-size: 2rem;
			text-align: center;
			margin-top: 3rem;
			// color: lighten(grey,37%);
			color: #fff;
			// color: lighten(black,10%);
		}

		button
		{
			padding: .3rem 2rem;
			font-size: 1.8rem;
			border: none;
			background: lighten(grey, 43%);
			border-radius: .8rem;
			// color: darken(#7dbfa2, 10%);
		}

		.boxs
		{
			margin-top: 2rem;
			display: flex;
			justify-content: center;

			.box1
			{
				padding-right: 3rem;
			}

			.box2
			{
				padding-left: 3rem;
				padding-right: 3rem;
				border-right: 1px solid lighten(grey, 10%);
				border-left: 1px solid lighten(grey, 10%);
			}

			.box3
			{
				padding-left: 3rem;
			}


			> div
			{
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;

				> h2
				{
					font-size: 2.4rem;
    				font-family: cursive;
				}

				> p
				{
					font-size: 1.8rem;
					color: lighten(grey, 5%);
					margin-top: .3rem;
				}

				> svg 
				{
					font-size: 2.5rem;
				}
			}
		}

		.stars
		{
			margin-top: 1.8rem;
		}
	}

	.button
	{
		margin-top: 2rem;
		width: 100%;
		// border-top: 1px solid lighten(grey,35%);
		text-align: center;
		position: absolute;
		left: 0;
		bottom: 5%;
		a
		{
			display: block;
			padding: 1.5rem 0;
			text-align: center;
			font-size: 2rem;
			width: 100%;
			height: 100%;
			transition: .5s;
			color: #000;
			border-radius: 0 0 1rem 1rem;
			// color: lighten(grey,37%);
			color: #fff;
		}
	}
}

@media screen and (max-width: 1500px)
{
	.card
	{
		width: 34rem !important;
	}
}