@use './materials/mixin' as *;

.header_section
{
	// max-width:  124rem;
	// margin: auto;
	padding: 1rem 3rem;
	position: relative;
	// background-color: #fcf4f0;
	background: #F4EFE3;
	// box-shadow: 1px 1px 10px lighten(grey,10%);

	.container 
	{
		max-width: 180rem;
		margin: auto;
		// padding: 0 1rem;

		// slider pending

		.slider
		{
			position: absolute;
			top: 0;
			left: -100%;
			transition: .5s;
			width: 35rem;
			height: 100vh;
			background: #ffffff;
			z-index: 100;
			padding: 3rem;
			// box-shadow: 1px 1px 4px 1px lighten(grey, 30%);

			.logo h2
			{
				text-align: center;
    			font-size: 3.5rem;
    			font-family: sans-serif;
			}

			.user
			{
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				margin-top: 5rem;

				.avatarBox
				{
					position: relative;
					display: flex;
					justify-content: center;
					margin-bottom: 1rem;
					img
					{
						width: 13rem;
						height: 13rem;
						border-radius: 50%;
					}
				}

				.details
				{
					display: flex;
					justify-content: center;
					flex-direction: column;
					align-items: center;

					h2
					{
						font-size: 2rem;
					}

					p
					{
						font-size: 1.8rem;
						color: lighten(grey, 10%);
					}
				}
			}

			// navbar 
			.navbar
			{
				margin-top: 3rem;
				display: flex;
				justify-content: center;
				align-items: center;
				height: 70vh;

				ul
				{
					display: flex;
					flex-direction: column;
					justify-content: center;
					gap: 1.8rem;

					li
					{
						display: flex;
						// justify-content: center;
						align-items: center;
						gap: .8rem;
						position: relative;

						a
						{
							font-size: 1.8rem;
							color: lighten(grey, 10%);
							transition: .5s;
						}

						svg
						{
							font-size: 1.8rem;
							color: lighten(grey, 10%);
							transition: .5s;
						}

						&:hover svg
						{
							color: #db3529;
						}

						&:hover a
						{
							color: #db3529;
						}
					}

					li:first-child svg
					{
						font-size: 1.5em;
					}
				}
			}
		}

		// topbar
		.topBar
		{
			display: flex;
			justify-content: space-between;
			align-items: center;
			.logo
			{
				width: 30rem;
				margin-left: -3.9rem;
			}

			.navbar
			{
				display: flex;
				justify-content: flex-end;
				align-items: center;
				width: 100%;
    			// padding: 0 2rem;
			}

			// left side 
			.left
			{
				display: flex;
				align-items: center;
				gap: 5rem;

				ul
				{
					display: flex;
					flex-direction: row;
					justify-content: center;
					align-items: center;
					gap: 2.5rem;
					list-style: none;

					li
					{
						position: relative;
						> p
						{
							width: 5rem;
							height: 5rem;
							background: #e74c3c;
							border-radius: 50%;
							font-size: 2.5rem;
							display: grid;
							place-items: center;
							color: #000;
						}
						div
						{
							position: absolute;
							top: 100%;
							left: -70%;
							padding: 2rem;
							border-radius: 1rem;
							z-index: 10;
							width: 14rem;
							box-shadow: 1px 1px 10px lighten(grey,10%);
							display: flex;
							flex-direction: column;
							gap: 1rem;
							justify-content: center;
    						align-items: flex-start;
							display: none;
							background: #fff;
							li a
							{
								font-size: 1.8rem;
							}
						}

						a
						{
							font-size: 2.2rem;
							color: lighten(black, 10%);
							transition: .5s;
							display: grid;
							place-items: center;
							&:hover
							{
								color: #db3529;
							}
						}

						svg
						{
							font-size: 5rem;
							color: lighten(#000, 5%);
							transition: .5s;
							&:hover
							{
								cursor: pointer;
							}
						}

						&:hover div
						{
							display: flex;
						}
					}

					// li:first-child svg
					// {
					// 	font-size: 1.5em;
					// }
				}

				// burger menu
				.burgerMenu
				{
					font-size: 4.5rem;
					display: grid;
					place-items: center;
					position: relative;
					// z-index: 1000;
					transition: .5s;

					&:hover
					{
						color: #db3529;
						cursor: pointer;
					}
				}

				// search box
				.searchBox
				{
					display: flex;
					align-items: center;
					padding-bottom: .5rem;
					border-bottom: .1rem solid #e6ddd9;

					p
					{
						font-size: 2rem;
						display: grid;
						place-items: center;
					}

					input
					{
						padding: 1rem;
						border: none;
						background: none;
						outline: none;
						width: 100%;
						font-size: 1.5rem;
					}
				}
			}


			// right 
			.right
			{
				display: flex;
				align-items: center;
				gap: 2rem;

				button
				{
					display: grid;
					place-items: center;
					background: none;
					border: none;
					outline: none;
					font-size: 2.5rem;
					color: #2b1f1f;
					transition: .5s;

					&:hover
					{
						color: darken(#2b1f1f, 50%);
						cursor: pointer;
					}
				}

				a
				{
					display: block;
					text-align: center;
					font-size: 1.8rem;
					width: 20rem;
					padding: 1rem;
					color: #ffffff;
					background: #db3529;
					border-radius: 1.5rem;
				}
			}
		}
	}
}

@media (max-width: 841px)
{
	.header_section
	{
		.container
		{
			.topBar
			{
				flex-direction: column;
				justify-content: center;
				.logo
				{
					margin-bottom: 2rem;
				}
				.navbar
				{
					flex-direction: column;
					justify-content: center;
				}
			}
		}
	} 
}

@media (max-width: 550px)
{
	.header_section
	{
		.container
		{
			.topBar .navbar
			{
				flex-direction: column;
				justify-content: center;

				ul
				{
					margin-bottom: 2rem;
				}
			}
		}
	} 
}