@use './materials/mixin' as *;

.section_home
{
	width: 100vw;
	min-height: 100vh;
	// background: #fcf4f0;
	background: #F4EFE3;
	position: relative;
	padding: 5rem 3rem;
	padding-top: 2rem;

	.container
	{
		max-width: 180rem;
		// height: 100vh;
		margin: auto;

		.topSide
		{
			display: grid;
			// place-items: center;
			grid-template-columns: repeat(2,1fr);
			position: relative;

			.image
			{
				width: 100%;
				height: 40rem;
				position: relative;
				img
				{
					width: 100%;
					height: 100%;
					border-radius: 1rem;
					position: absolute;
					top: 0;
					left: 0;
				}
			}

			.content
			{
				padding-left: 5rem;
				display: flex;
				padding-top: 5rem;
				flex-direction: column;
				h2
				{
					font-size: 4rem;
					color: #000;
					margin-bottom: 1rem;
				}

				p
				{
					font-size: 2rem;
				    color: #000;
				    opacity: 0.7;
				}
			}
		}

		.cardBox
		{
			margin-top: 2rem;
			display: grid;
			grid-template-columns: repeat(4,1fr);
			row-gap: 3rem;
			place-items: center;
			column-gap: 3rem;
		}

		.buttonBox
		{
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			height: 100%;

			a
			{
				display: block;
				width: 40rem;
				padding: 1rem;
				border-radius: 2rem;
				text-align: center;
				background: lighten(grey, 45%);
				color: #000;
				font-size: 2rem;
				margin: 2rem;
				transition: .5s;

				&:hover
				{
					background: lighten(grey, 5%);
				}
			}
		}
	}
}

@media (max-width: 1300px)
{
	.section_home
	{
		.container
		{
			.cardBox
			{
				grid-template-columns: repeat(3,1fr);
			}
		}
	} 
}


@media (max-width: 994px)
{
	.section_home
	{
		.container
		{
			.cardBox
			{
				grid-template-columns: repeat(2,1fr);
			}
		}
	} 
}

@media (max-width: 800px)
{
	.section_home
	{
		padding-top: 1rem;
		.container
		{
			.topSide
			{
				grid-template-columns: 1fr;
				.content
				{
					padding-left: 0;
					padding-top: 2rem;
					h2
					{
						text-align: center;
					}
					p
					{
						text-align: center;
					}
				}
			}
		}
	}
}

@media (max-width: 700px)
{
	.section_home
	{
		.container
		{
			.cardBox
			{
				grid-template-columns: repeat(2,1fr);
			}
		}
	} 
}


@media (max-width: 650px)
{
	.section_home
	{
		.container
		{
			.cardBox
			{
				grid-template-columns: 1fr;
			}
		}
	} 
}

@media (max-width: 420px)
{
	.section_home
	{
		padding: 5rem 1rem;
		.container
		{
			.topSide .left .text h2 
			{
				font-size: 4rem;
			}
		}
	} 
}