
.customize-chararacter
{
	width: 100vw;
	overflow-x: hidden;
	min-height: 100vh;
	background: #F4EFE3;
	padding: 1rem 3rem;

	.container
	{
		max-width: 180rem;
		margin: auto
	}

	.top-section
	{
		display: grid;
		grid-template-columns: 4fr 3fr;
		gap: 2rem;
		// place-items: center;
		// margin: 2rem 0;
		padding: 2rem 0;

		.image-box
		{
			width: 100%;
			height: 100%;
			position: relative;
			img
			{
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				border-radius: .4rem;
				object-fit: contain;
			}
		}

		.content-box
		{
			display: flex;
			justify-content: flex-end;
			align-items: center;
			flex-direction: column;
			gap: 2rem;
			padding: 3rem 2rem;

			.heading
			{
				font-size: 3rem;
				font-weight: bold;
				color: #000;
			}

			.para
			{
				font-size: 1.8rem;
				color: #000;
				opacity: .7;
				// max-width: 55rem;
			}
			.buttons
			{
				margin-top: 2rem;
				width: 100%;
				display: flex;
				// justify-content: space-around;
				gap: 5rem;
				// padding: 0 3rem;
				button
				{
					padding: 1rem 3rem;
					background: transparent;
					// border: 1px solid #000;
					border: none;
					border-radius: 2.2rem;
					font-size: 1.5rem;
					transition: .5s;
					width: 20rem;
					text-align: center;
					color: #fff;
				}

				button:nth-child(1)
				{
					background: darken(#9cbb9c,20%);
					&:hover
					{
						background: darken(#9cbb9c,30%);
						color: #fff;
					}
				}
				button:nth-child(2)
				{
					// background: darken(#d7875e,20%);
					background: #D68A63;
					&:hover
					{
						background: darken(#D68A63,10%);
						color: #fff;
					}
				}
			}
		}
	}
	.box
	{
		margin-top: 3rem;
		padding: 0;
		h2
		{
			font-size: 2.5rem;
		}
	}

	.card-box
	{
		margin-top: 2rem;
		display: grid;
		place-items: center;
		grid-template-columns: repeat(4,1fr);
		row-gap: 3rem;
		column-gap: 3rem;

		.character-card
		{
			width: 43rem;
			position: relative;
			min-height: 45rem;
			// background: #F5DCB3;
			border-radius: 1rem;
			box-shadow: 1px 1px 10px lighten(grey,40%);
			.image
			{
				width: 100%;
				height: 33rem;
				position: relative;
				img
				{
					width: 100%;
					height: 100%;
					// object-fit: contain;
					position: absolute;
					top: 0;
					left: 0;
					border-radius: 1rem 1rem 0rem 0rem;
				}
			}
			.content
			{
				padding: 2rem;
				background: #F5DCB3;
    			border-radius: 0rem 0rem 1rem 1rem;
    			height: 13rem;
				h3
				{
					font-size: 2.2rem;
					text-align: center;
					margin-bottom: .7rem;
					color: #000;
				}
				p
				{
					font-size: 1.8rem;
					text-align: center;
					color: #000;
					opacity: .7;
				}
			}

			.buttons
			{
				width: 100%;
				// display: grid;
				// grid-template-columns: 1fr 1fr;
				// gap: .3rem;
				margin-top: 2rem;
				display: flex;
    			justify-content: center;
				button
				{
					padding: 1rem;
					border-radius: 2rem;
					// border: 1px solid #000;
					background: #9CBA9E !important;
					border: none;
					color: #fff;
					width: 20rem;
					font-size: 2rem;
					background: transparent;
					transition: .5s;
					&:hover
					{
						background: #000;
						color: #fff;
					}
				}
			}
		}
	}
}

@media (max-width: 1500px){
	.card-box .character-card
	{
		width: 34rem !important;
	}
}

@media (max-width: 1046px){
	.customize-chararacter .container .top-section .content-box .buttons
	{
		flex-direction: column;
		justify-content: center;
		gap: 2rem;
	}

	.customize-chararacter .container .card-box
	{
		grid-template-columns: repeat(2,1fr);
		row-gap: 3rem;
	}
}

@media (max-width: 800px){
	.customize-chararacter
	{
		padding:2rem;
	}
	.customize-chararacter .container .top-section
	{
		grid-template-columns: 1fr;
		.content-box
		{
			padding: 1rem;
			.buttons
			{
				align-items: center;
				button
				{
					width: 30rem;
					max-width: 30rem;
				}
			}
		}
	}
}

@media (max-width: 550px){
	.customize-chararacter .container .card-box
	{
		grid-template-columns: 1fr;
	}
}