@use './materials/mixin' as *;


.section_dashboard
{
	padding-top: 12rem;
	padding-bottom: 10rem;
	min-height: 100vh;
	background-color: var(--background-primary);

	.container
	{
		max-width: 110rem;
		margin: auto;
		position: relative;

		.buttonBox
		{
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 2rem 0;

			a 
			{
				width: 20rem;
				padding: 1rem;
				border-radius: 2rem;
				border: none;
				background-color: #000;
				color: #fff;
				font-size: 1.7rem;
				margin: 0 1.5rem;
				transition: .7s;
				text-align: center;

				&:hover
				{
					background-color: #fff;
					color: lighten(grey, 10%);
					border: 1px solid lighten(grey, 15%);
				}
			}
		}

		.postBox
		{
			margin-top: 2rem;
			.heading
			{
				font-size: 3rem;
			}

			.Posts
			{
				margin: 4rem 0;
				display: grid;
				grid-template-columns: repeat(3,1fr);
				gap: 2rem;
				.card
				{
					width: 35rem;
					background-color: var(--background-secondary);
					padding-bottom: 1rem;
					cursor: pointer;
					transition: .5s;
					&:hover
					{
						transform: translateY(-1rem);
					}
					.image
					{
						width: 100%;
						position: relative;

						img 
						{
							width: 100%;
							object-fit: cover;
							height: 33rem;
							border-radius: .8rem;
						}
					}

					.details
					{
						padding: 1rem .8rem;

						.title 
						{
							font-size: 2rem;
						}

						.text
						{
							font-size: 1.4rem;
							margin-top: .8rem;
						}
					}
				}
			}
		}
	}
}