.section-admin-login
{
	width: 100vw;
	padding: 0 2rem;
	background-color: #fcf4f0;
	min-height: 100vh;

	.container
	{
		max-width: 225rem;
		min-height: 100vh;
		margin: auto;
		display: grid;
		place-items: center;

		.login-form
		{
			width: 60%;
			background-color: #fff;
			padding: 3rem;
			border-radius: 1.5rem;
			position: relative;
			box-shadow: 1px 1px 10px lighten(grey, 35%);
			text-align: center;
			padding-top: 0;

			.icon
			{
				border-radius: 50%;
				display: grid;
				place-items: center;
				background-color: lighten(grey, 45%);
				width: 10rem;
				height: 10rem;
				margin: auto;
				transform: translateY(-5rem);
				box-shadow: 1px 1px 10px lighten(grey, 20%);

				svg
				{
					font-size: 7rem;
				}
			}

			.inputs
			{
				margin-top: -5rem;
			}

			.options
			{
				width: 100%;
				height: 100%;

				> p
				{
					font-size: 1.8rem;
					text-align: left;
					padding: 0 2rem;

					a
					{
						color: #db3529;
						transition: .5s;

						&:hover
						{
							color: lighten(blue, 10%);
						}
					}
				}
			}
			.radio
			{
				flex-direction: row !important;
				justify-content: flex-start !important;
				gap: 3rem;
				> p 
				{
					display: flex;
					align-items: center;
					font-size: 1.8rem;
					gap: .5rem;
				}
			}

			.use
			{
				flex-direction: row !important;

				input
				{
					width: 90% !important; 
				}

				button
				{
					border-radius: 50%;
					border: none;
					font-size: 2rem;
					padding: .5rem 1rem;
					margin-left: .5rem;
				}

				.delete
				{
					width: 4rem;
					height: 4rem;
					font-family: 2rem;
					display: grid;
					place-items: center;
					padding: 0;
					color: lighten(red,5%);
					transition: .5s;
					&:hover
					{
						color: lighten(red,1%);
					}
				}
			}

			.field
			{
				margin: 3.5rem 0;
				position: relative;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;

				textarea
				{
					padding: 2rem;
				  width: 95.5%;
				  border-radius: .7rem;
				  border: none;
				  background-color: lighten(grey, 45%);
				  outline: none;
				  font-size: 1.7rem;
				  height: 25rem;
				}

				.addCharacterBox
    			{
    				padding: 1rem;
    				border: .1rem solid lighten(grey, 20%);
    				border-radius: 1rem;
    				padding-bottom: 3rem;
    			}

    			.two_inputs
    			{
    				width: 100%;
    				height: 100%;
    				display: grid;
    				position: relative;
    				grid-template-columns: 1fr;
    				input
    				{
    					width: 50%;
    				}
    			}
    			.add 
    			{
    				display: flex;
    				justify-content: center;
						align-items: center;
						gap: 1rem;
						margin-top: 2rem;

						input[type="file"]
						{
							border: 1px solid;
							border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
							padding: 1.5rem;
						}
    			}

				> h2 
				{
					font-size: 4rem;
				}

				.loader {
				  border: 1rem solid #f3f3f3; 
				  border-top: 1rem solid #3498db;
				  border-radius: 50%;
				  width: 5rem;
				  height: 5rem;
				  animation: spin 2s linear infinite;
				  margin-bottom: 1rem;
				}

				.error
				{
					font-size: 1.8rem;
					text-align: center;
					color: red;
					margin-bottom: 1rem;
				}


				> img
				{
					width: 20rem;
					height: 20rem;
				}

				> div
				{
					display: flex;
					justify-content: center;
					align-items: center;
					position: relative;
					gap: 1rem;
					width: 100%;
					height: 100%;

					.addBtn
					{
						font-size: 2rem;
						width: 2.5rem;
						height: 2.5rem;
						display: grid;
						place-items: center;
						border-radius: 50%;
						// padding: 1rem 2rem;
						border: 1px solid #000;
						background-color: white;
						cursor: pointer;
					}
				}

				input 
    			{
				    padding: 2rem;
				    width: 100%;
				    max-width: 70rem;
				    border-radius: .7rem;
				    border: none;
				    background-color: lighten(grey, 45%);
				    outline: none;
				    font-size: 1.7rem;
    			}

    			.btn
				{
					display: block;
					text-align: center;
					font-size: 1.8rem;
					width: 20rem;
					padding: 1rem;
					background: #db3529;
					color: lighten(grey, 45%);
					border-radius: 1rem;
					border: none;
					cursor: pointer;
					margin: 0;
					transition: .5s;

					&:hover
					{
						color: #db3529;
						background: lighten(grey, 45%);
					}
				}
			}
		}
	}
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media (max-width: 700px){
	.section-admin-login .container .login-form
	{
		width: 100%;
	}
}