
body
{
	background: #F4EFE3;
}
.profile-section
{
	background-image: url('../../public/images/jungle.avif') !important;
	background-repeat: no-repeat !important;
	background-size: cover !important;
	width: 100vw;
	min-height: 100vh;
	display: block !important;


	.container
	{
		width: 180rem;
    	max-width: 96.6%;
		margin: auto;
		height: 100vh;
		min-height: 100vh;
		// display: flex;
		// background: #F4EFE3;
		position: relative;
	}
	.para
	{
		margin-bottom: 5rem !important;
		margin-top: .5rem;
		font-size: 1.7rem !important;
	}

	.other
	{
		border-bottom: .2rem solid lighten(black,30%) !important;

		.other-field
		{
			display: flex;
			justify-content: space-between;
			align-items: center;

			h2 
			{
				font-size: 1.5rem !important;
				color: lighten(black,30%) !important;
			}

			h2.active
			{
				color: #d3826c !important;
			}
		}
	}

	.content
	{
		// background: lighten(#ad8f7a,35%);
		background: #F4EFE3;
	}

	.form-box h1
	{
		color: lighten(black,30%) !important;
		margin-bottom: 5rem !important;
	}

	.image::before
	{
		display: none !important;
	}

	.image img
	{
		object-fit: cover !important;
	}

	.content .form-box input[type='text'],
	.content .form-box input[type='password']
	{
		border: none !important;
		border-radius: 0 !important;
	}

	.content .form-box input[type='submit']
	{
		width: 18rem !important;
		min-width: 18rem !important;
		background: darken(#d3826c,15%) !important;
	}

	.content .form-box .field.submit
	{
		display: flex;
		justify-content: space-between;
		align-items: center;

		a
		{
			width: 18rem;
			min-width: 18rem;
			background: transparent;
			color: lighten(black, 20%);
			outline: none;
			border: .1rem solid lighten(black, 20%);
			font-weight: 500;
			cursor: pointer;
			font-size: 2rem;
			transition: .5s;
			text-align: center;
			padding: .8rem 0;
			border-radius: 3rem;

			&:hover
			{
				background: darken(#000,10%);
				color: #fff;
			}
		}
	}
	.content .form-box .field.input
	{
		margin-bottom: 3rem !important;
		border-bottom: .2rem solid lighten(black,30%) !important;
		display: flex;

		button
		{
			background: none;
			border: none;
			font-size: 2rem;
			outline: none;
			color: lighten(black,30%);
		}
	}
}


.section-login
{
	width: 100vw;
	height: 100vh;
	min-height: 100vh;
	display: flex;
	background: #F4EFE3;
	position: relative;
	.image
	{
		width: 50%;
		height: 100%;
		position: relative;
		object-fit: cover;
		&::before
		{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(225deg, #e91e63,#03a9f4);
			z-index: 1;
			mix-blend-mode: screen;
		}
		img
		{
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	.content
	{
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50%;
		height: 100%;

		.form-box
		{
			width: 65%;

			h1
			{
				color: #000;
				font-weight: 600;
				font-size: 4rem;
				// border-bottom: .4rem solid #db3529;
				letter-spacing: 1px;
				margin-bottom: 2rem;
			}

			p
			{
				font-size: 1.7rem;
				color: #000;
				line-height: 1.6;
				opacity: .7;
			}

			h2
			{
				color: #000;
				font-weight: 600;
				font-size: 2.2rem;
				margin-bottom: 2rem;
				// border-bottom: .4rem solid #db3529;
				display: inline-block;
				letter-spacing: 1px;
			}

			.field
			{
				margin-bottom: 2rem;
				display: flex;

				p.pppp
				{
					font-size: 1.2rem !important;
					margin-top: -.4rem;
					margin-left: .5rem;
				}
			}

			.field input
			{
				width: 100%;
				padding: 1rem 2rem;
				outline: none;
				font-weight: 400;
				border: .1rem solid #607d8b;
				font-size: 1.6rem;
				letter-spacing: .1rem;
				color: #000;
				border-radius: 3rem;
				background: transparent;
			}

			.field input[type="submit"]
			{
				background: #db3529;
				color: #fff;
				outline: none;
				border: none;
				font-weight: 500;
				cursor: pointer;
				font-size: 2rem;
				transition: .5s;

				&:hover
				{
					background: darken(#db3529,10%);
				}
			}

			.field p
			{
				color: #000;
				font-size: 1.8rem;

				a
				{
					color: #db3529;
				}
			}
		}
	}
}


@media (max-width: 1600px)
{
	.profile-section
	{
		.container
		{
			max-width: 96%;
		}
	}

}

@media (max-width: 1350px)
{
	.profile-section
	{
		.container
		{
			max-width: 95.5%;
		}
	}

}


@media (max-width: 800px)
{
	.section-login
	{
		.image
		{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		.content
		{
			width: 100%;
			height: 100%;
			z-index: 1;

			.form-box
			{
				width: 100%;
				padding: 4rem;
				background: rgba(255, 255, 255, .9);
				margin: 5rem;
			}
		}
	}

	.profile-section
	{
		.content
		{
			.form-box
			{
				background: transparent !important;
			}
		}
	}
}


@media (max-width: 600px)
{
	.profile-section .content .form-box .field.submit
	{
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 2rem;
	}
}