.section_container
{
	padding: 5rem;
	background-color: #fcf4f0;
	.container
	{
		max-width: 125rem;
		margin: auto;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;

		.page
		{
			// padding: 2rem;
		    color: #785e3a;
		    overflow: hidden;
		    box-shadow: 1px 1px 10px lighten(grey,10%);
		    background-color: #fff;
		    position: relative;
		    .btnBox
		    {
		    	width: 100%;
		    	height: 100%;

		    	button
		    	{
		    		display: block;
					text-align: center;
					font-size: 1.8rem;
					width: 20rem;
					padding: 1rem;
					background: lighten(grey, 45%);
					border-radius: 1rem;
					border: none;
					cursor: pointer;
					transition: .5s;
					background: #db3529;
					color: lighten(grey, 45%);
		    	}
		    }
		}

		.page-cover
		{
			background-color: #e3d0b5;
		    color: #785e3a;
		    border: 1px solid #998466;
		    display: grid !important;
		    place-items: center;

		    h2
		    {
		    	text-align: center;
		    	font-size: 4rem;
		    }
		}

		.page-content
		{
			// padding: 2rem;
		}

		.page-header
		{
			height: 3rem;
    		font-size: 2rem;
    		text-transform: uppercase;
    		text-align: center;
		}

		.page-image
		{
			height: 50rem;
			width: 100%;
			position: relative;
			// margin-top: 1rem;

			img
			{
				width: 100%;
				height: 100%;
				object-fit: initial;

			}

			.avatar
			{
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;

				> img
				{
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
					position: absolute;
					object-fit: initial;
				}
			}
		}

		.page-text
		{
			width: 100%;
			font-size: 1.5rem;
			color: lighten(black, 30%);
			margin-top: 2rem;
			text-align: left;
			line-height: 1.6;
			padding: 0 2rem;
		}

		.page-footer
		{
			padding: .2rem;
			margin-top: .7rem;
			font-size: 1.5rem;
			border-top: 1px solid lighten(black, 30%);
		}

		.character-page
		{
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;
			left: 0;
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			align-items: center;
			overflow-y: auto;

			.avatar
			{
				// width: 20rem;
				// height: 20rem;
				width: 100%;
				height: 100%;
				position: relative;
				img
				{
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
				}
			}

			> img
			{
				width: 15rem;
				height: 15rem;
			}
		}

		.demoPage
		{
			box-shadow: 1px 1px 10px 1px lighten(grey,45%);
			position: relative;
			> img 
			{
				width: 100%;
				height: 100%;
			}

			.title
			{
				position: absolute;
				bottom: 0%;
				left: 0;
				width: 100%;
				z-index: 100;
				background: #fff;
				// border: .2rem solid #000;
				padding: 1rem 0;

				> h1
				{
					font-size: 2.5rem;
					text-align: center;
					// padding-top: 5rem;
				}
			}
		}

		.btnBox
		{
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 1rem;
			margin-top: 2rem;
			.btn
			{
				display: block;
				text-align: center;
				font-size: 1.8rem;
				width: 20rem;
				padding: 1rem;
				color: #db3529;
				background: lighten(grey, 45%);
				border-radius: 1rem;
				border: none;
				cursor: pointer;
				transition: .5s;
				background: #db3529;
				color: lighten(grey, 45%);

				&:hover
				{
					background: #db3529;
					color: lighten(grey, 45%);
				}
			}

			.btn:first-child
			{
				background: #7dbfa2;
				color: lighten(grey, 45%);
				&:hover
				{
					background: #7dbfa2;
					color: lighten(grey, 45%);
				}
			}

			.text
			{
				font-size: 2rem;
			}
		}
	}
}

@media (max-width: 950px){
	.section_container .container .page-image
	{
		height: 20rem;
	}
}

@media (max-width: 890px){
	.section_container .container .page-image
	{
		height: 20rem;
	}
}


@media (max-width: 775px){
	.section_container .container .btnBox .text
	{
		display: none;
	}

	.section_container .container .page-image
	{
		height: 20rem;
	}
}